import React, { Component } from 'react';
import { connect } from "react-redux";
import { Modal } from 'react-bootstrap';
import CreatableSelect from 'react-select/creatable';
import { postAddTag } from '../../../../Services/Actions/tagsActions'
import { AddTagAll, loadAssetsDataWithDispatch } from '../../../../Services/Actions/assetsActions'


class AddTagModal extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        tagString: [],
        newTag: "",
        listTags: [],
        selectedOption: null,
        isLoading: false
    };
    addTagValue = (value) => {

    }

    onClickToRemoveTag = (index) => event => {
        let { tagString } = this.state;
        tagString.splice(index, 1)
        this.setState({ tagString })

    }
    handleClose = () => {
        this.props.addTagCallback(false)
    };
    componentWillMount() {
        const { tagList } = this.props
        if (tagList && tagList.length > 0) {
            var listTags = []
            tagList.map((item) => {
                listTags.push({ value: item, label: item })
            })
            this.setState({ listTags })


        }

    }

    componentDidMount() {
        const { modalAssetData } = this.props
        if (modalAssetData && Object.keys(modalAssetData).length > 0) {
            if (modalAssetData.tags && String(modalAssetData.tags).length > 0) this.setState({ tagString: String(modalAssetData.tags).split(",") })
        }
    }

    onClickNewAddTag = event => {
        const { newTag, tagString } = this.state;
        const { modalAssetData } = this.props
        if (newTag && String(newTag).length >= 3) {
            tagString.push(newTag)
            this.setState({ tagString, newTag: "", selectedOption: null })
        }
        else {

        }
    }

    addNewTag = event => this.setState({ newTag: event.target.value })

    onClickToSave = event => {
        const { tagString, isLoading } = this.state;
        const { modalAssetData, addTagShowModal } = this.props;
        if (tagString && tagString.length > 0) {
            if (addTagShowModal && modalAssetData == null) {
                this.setState({ isLoading: true })
                this.props.AddTagAll(tagString.join(","), () => {
                    this.props.addTagCallback(false)
                    this.setState({ isLoading: false })
                })
            }
            else {
                this.setState({ isLoading: true })
                this.props.postAddTag({
                    "postId": String(modalAssetData.id),
                    "tags": tagString.join(",")
                }, () => {
                    this.setState({ isLoading: false })
                    this.props.loadAssetsDataWithDispatch()
                })
            }

        }

    }

    setNewTags = (selectedOption) => {

        if (selectedOption && selectedOption.value) this.setState({ newTag: selectedOption.value, selectedOption })
        else this.setState({ newTag: "", selectedOption: null })


    };
    render() {
        const { addTagShowModal, modalAssetData } = this.props
        const { tagString, newTag, listTags, selectedOption, isLoading } = this.state

        const imageStyle = {
            paddingBottom: '120%', width: '100%'
        };
        const tagsArrayFun = () => {
            const tagItem = tagString.map((tag, removeIndex) =>
                <div key={tag.toString()} className="badge bg-secondary text-gray-800 rounded-pill border me-2 mb-2">
                    <span className="text-break" style={{ whiteSpace: 'normal' }}>{tag}</span>
                    <button className="btn btn-sm p-0 ms-1" onClick={this.onClickToRemoveTag(removeIndex)}>
                        <i className="ri-close-line icon-7"></i>
                    </button>
                </div>
            );
            return tagItem;
        }
        const status = modalAssetData && Object.keys(modalAssetData).length > 0 ? true : false

        return addTagShowModal ? <Modal show={addTagShowModal} onHide={this.handleClose}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title>Add Tag</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="mx-xl-4 px-2 px-lg-4 mx-0 pt-4">
                    <div className="row">
                        {status ? <div className="col-lg-5">
                            <div className="card-body d-flex flex-column mb-3">
                                <div className="d-flex align-items-center cursor-pointer">
                                    <div className="position-relative rounded-1 bg-light overflow-hidden w-100" style={imageStyle}>
                                        <img className="object-center-cover position-absolute" loading="lazy" src={modalAssetData.postFile} height={500} width={300} alt={'suit'} />
                                    </div>
                                </div>
                            </div>
                        </div> : null}
                        <div className={`col-lg-${status ? `7` : `12`}`}>
                            <div className="mb-3 mb-xl-4">
                                <label className="text-muted fw-semibold mb-2">Add Tag</label>
                                <div className="d-flex align-items-center w-100">
                                    <div className="position-relative d-flex flex-grow-1 flex-column pe-2">
                                        <CreatableSelect isClearable options={listTags} onChange={this.setNewTags} value={selectedOption} />
                                    </div>
                                    <button type="button" className="btn btn-primary" onClick={this.onClickNewAddTag}>Add</button>
                                </div>
                            </div>
                            <div className="mb-3 mb-xl-4">
                                <div style={{ minHeight: 220 }}>
                                    <div className="tags-group d-flex align-items-center flex-wrap">
                                        {tagsArrayFun()}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="d-flex alert alert-info">
                        <div className="d-flex flex-column flex-stack flex-grow-1">
                            <p className="mb-0 text-gray-800">Tags will help you improve content filtering.</p>
                        </div>
                    </div>
                </div>
            </Modal.Body>
            <Modal.Footer className="p-3">
                <div className="mx-xl-4 px-2 px-lg-4 mx-0">
                    <button type="button" className="btn btn-secondary btn-sm btn-min-w me-3" onClick={this.handleClose}>Cancel</button>
                    <button type="button" className={`btn btn-primary btn-sm btn-min-w ${isLoading ? `spinner` : ``}`} onClick={this.onClickToSave}>Save</button>
                </div>
            </Modal.Footer>
        </Modal> : null
    }
}
const mapStateToProps = (state) => {
    return {
        tagList: state.tags.tagList
    }
}

const mapDispatchToProps = dispatch => {
    return {
        postAddTag: (data, callback) => dispatch(postAddTag(data, callback)),
        AddTagAll: (data, callback) => dispatch(AddTagAll(data, callback)),
        loadAssetsDataWithDispatch: () => dispatch(loadAssetsDataWithDispatch()),

    }
}
export default connect(mapStateToProps, mapDispatchToProps)(AddTagModal);
